.persona-header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    overflow: hidden;

    min-width: 945px;
    padding: 13px;
    border-radius: 22px;

    box-shadow: 0px 0px 45px rgba(0, 0, 0, 0.08);
}

.persona-home-button {
    width: 150px;
    padding-left: 20px;
}

.persona-header-name {
    margin-right: auto;
    width: 190px;
}

.persona-header-name-mobile {
    display: none;
    text-align: center;
}

.persona-header-type {
    margin-left: auto;
    width: 190px;
    text-align: right;
}

.persona-header-avatar {
    width: 150px;
    height: 77px;
}

.persona-motto {
    margin-top: var(--h-size-6);
    margin-bottom: calc(var(--h-size-6)*2);

    font-style: italic;
    font-weight: 300;
    font-size: 16px;
    text-align: center;
}

.persona-section-title {
    border-bottom: 2px solid #51596A;
    margin-top: var(--h-size-6);
    margin-bottom: var(--h-size-6);
}

.persona-section-title h3 {
    text-align: center;
    font-size: 28px;
    margin-block-start: 0px;
    margin-block-end: 5px;
}

.persona-section-content {
    display: flex;
    gap: 30px;
    justify-content: center;

    margin-top: var(--h-size-4);
    margin-bottom: var(--h-size-4);
}

.persona-section-item-icon {
    display: flex;
    height: var(--h-size-5);
    justify-content: center;
    margin-bottom: 3px;
}

.persona-section-item-title {
    font-weight: 600;
    font-size: 25px;
    line-height: 38px;
    text-align: center;
}

.persona-section-item-snippet {
    margin-bottom: 10px;

    font-weight: 400;
    font-size: 17px;
    line-height: 23px;
    text-align: center;
}

.persona-section-item-description {
    max-width: var(--h-size-1);
    font-weight: 400;
    font-size: 13px;
    line-height: 14px;
    text-align: center;
}

@media (max-width: 600px) {
    .persona-header {
        flex-flow: column;
        height: var(--h-size-1);
        min-width: calc(var(--h-size-1) * var(--ratio)) !important;
    }

    .persona-home-button,
    .persona-header-name,
    .persona-header-type,
    .persona-header-avatar {
        display: none;
    }

    .persona-header-name-mobile {
        display: block;
    }

    .persona-header-icon img,
    svg {
        width: calc(var(--h-size-3)*1.25) !important;
        height: calc(var(--h-size-3)*1.25) !important;
    }

    .persona-section-content {
        flex-flow: column;
    }

    .persona-section-item-description {
        display: flex;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
    }
}